
// Шрифты ====================================================================
@import url('https://fonts.googleapis.com/css?family=Fira+Sans:100,300,400,700,800|Lato:700&display=swap');

// ===========================================================================


body {

}

.wrapper {
	overflow: hidden;
	min-height: 100%;
}

.container {
	max-width: 1180px;
	padding: 0;
	margin: 0 auto;
	@media (max-width: 1210px) {
		padding: 0 15px;
	}
}






/* ==========================================================================
HEADER
========================================================================== */


.header {
	position: absolute;
	width: 100%;
	z-index: 50;
	left: 0;
	top: 85px;

	&__row {
		margin: 0 0 15.5%;
	}

	&__menu {}


	&__content {
		max-width: 540px;
	}

	&__title {
		font-size: 72px;
		line-height: 1.2;
		font-weight: 300;
		letter-spacing: -0.025em;
		color: #000000;
		margin: 0 0 15px;
	}

	&__text {
		font-size: 30px;
		line-height: 45px;
		font-weight: 300;
		letter-spacing: -0.025em;
		color: #000000;
	}

	@media (max-width: 1275px) {

		&__row {
			margin: 0 0 11%;
		}
		&__title {
			font-size: 40px;
		}
		&__text {
			font-size: 25px;
			line-height: 37px;
		}
	}
	
}


.menu {
	@include flexbox();
	justify-content: space-between;
	align-items: center;
	height: 85px;

	&__body {
		@include flexbox();
		justify-content: center;
		align-items: center;
		width: 80px;
		height: 80px;
		background-color: #000000;
	}

	&__link {
		font-size: 60px;
		line-height: 1.2;
		font-weight: 800;
		color: #ffffff;
		text-align: center;
	}

	&__icon {}

	@media (max-width: 767px) {
		&__body {
			width: 50px;
			height: 50px;
		}
		&__link {
			font-size: 38px;
		}
	}
}

.icon-menu {
	@include flexbox();
	flex-direction: column;
	justify-content: center;
	align-items: center;
	position: relative;
	cursor: pointer;
	margin: 0 23px 0 0;
	span {
		width: 27px;
		height: 5px;
		color: #000;
		z-index: 5;
	}
	&__bars {
		margin: 0 0 7px 0;
		&:link {color:#141414;}
	}
	&__text {
		font-size: 12px;
		line-height: 1.2;
		font-weight: 400;
		text-transform: uppercase;
		color: #141414;
	}
	&__dropdown {
		display: none;
		position: absolute;
		right: 0;
		width: max-content;
		background: #282b2f;
		list-style-type: none;

		li a {
			display: inline-block;
			font-size: 16px;
			padding: 10px 20px;
			color: #fff;
			text-decoration: none;
		}

		li:hover {
			background: #666666;
		}
	}
}

i {
	font-size: 30px;
}

.show {
	display: block;
}


@media (max-width: 767px) {
	body.lock {
		overflow: hidden;
	}
}




/* ==========================================================================
Main-screen
========================================================================== */

.page {
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	@include flexbox();
	flex-direction: column;
}
.main-screen {
	flex: 1 0 100%;
	position: relative;
	&__bg {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
	}
	&__triangle {
		position: absolute;
		bottom: 0;
		right: 0;
		border-bottom: 900px solid #42b7c8;
		border-left: 1129px solid transparent;
		opacity: 0.3;
	}
}




/* ==========================================================================
CONTENT
========================================================================== */

.content{
	
	&__row {
		@include flexbox();
		
		&_b {
			background-image: url("../img/musician.jpg");
			background-repeat: no-repeat;
			background-size: cover;
			background-position: center;
		}
	}

	&__column {
		flex: 0 1 25%;

		&_b {
			flex: 0 1 50%;

			&__image {
				padding: 0 0 57.25%;
			}
		}

		&_l {
			z-index: 5;
		}

		&_s {
			padding: 0 0 25.063%;
		}
	}

	&__image {
		position: relative;
		padding: 0 0 128.8%;
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__block {

		height: 100%;
		&_white {
			background-color: #f3f1ef;
			padding: 13.5% 13.5% 8% 7%;
			.block__title, .block__text {
				color: #666666;
			}
		}

		&_blue {
			background-color: #3f729b;
			padding: 14.9% 5% 16.852% 26.3%;

			.block__text {
				margin: 0 0 52px 0;
			}
		}

		&_green {
			background-color: #2ebd59;
			padding: 16.6% 5% 20.402% 26.3%;

			.block__text {
				margin: 0 0 53px 0;
			}
		}

		&_dark {
			background-color: #282b2f;
			padding: 16.6% 13.5% 19.901% 7.4%;

			.block__text {
				margin: 0 0 55px 0;
			}
		}

	}

	@media (max-width: 1460px) {
		&__block {

			&_white {
				padding: 13.5% 13.5% 8% 7%;
			}
			&_blue {
				padding: 14.9% 5% 16.852% 15%;
			}
			&_green {
				padding: 16.6% 5% 20.402% 15%;
			}
			&_dark {
			padding: 16.6% 13.5% 19.901% 7.4%;
			}
		}
	}


	@media (max-width: 800px) {
		&__row {
			flex-wrap: wrap;
		}
		&__column {
		flex: 0 1 50%;
			&_b {
				flex: 0 1 100%;
			}
			&_s {
				padding: 0;
			}
		}
		&__block {

			&_white {
				padding: 13.5% 13.5% 8% 7%;
			}
			&_blue {
				padding: 13.5% 13.5% 16.852% 7%;
			}
			&_green {
				padding: 13.5% 13.5% 16.852% 7%;
			}
			&_dark {
				padding: 13.5% 13.5% 16.852% 7%;
			}
		}

	}

	@media (max-width: 480px) {
		&__column {
			flex: 0 1 100%;
		}
	}

}

.block {

	&__title {
		font-size: 24px;
		line-height: 1.2;
		font-weight: 700;
		text-transform: uppercase;
		color: #f9f9f9;
		margin: 0 0 31px 0;
	}

	&__subtitle {
		font-size: 18px;
		font-weight: 400;
		text-transform: uppercase;
	}

	&__text {
		font-size: 36px;
		line-height: 1.2;
		font-weight: 300;
		letter-spacing: -0.025em;
		color: #f9f9f9;
		max-width: 510px;
		margin: 0 0 50px 0;
	}

	&__button {}

	&_small {
		padding: 33.5% 14.5% 26% 14.5%;
		background-color: #212227;

		.block__title, .block__subtitle {
			color: #2ebd59;
		}

		.block__title {
			margin: 0 0 13px 0;
		}

		.block__subtitle {
			margin: 0 0 32px 0;
		}

		.block__text {
			font-size: 14px;
			line-height: 21px;
			font-weight: 400;
			color: #f9f9f9;
			letter-spacing: 0.002em;
			margin: 0 0 54px 0;
		}
	}

	@media (max-width: 1275px) {
		&__title {
			font-size: 20px;
		}
		&__text {
			font-size: 24px;
		}
		&_small {
			padding: 20.5% 7.5% 26% 7.5%;
		}
	}

	@media (max-width: 800px) {
		&_fade {
			display: none;
		}
		&_small {
			padding: 13.5% 13.5% 16.852% 7%;
		}
	}

	@media (max-width: 480px) {
		&_empty {
			display: none;
		}
	}

}

.button {
	font-size: 18px;
	line-height: 1.2;
	font-weight: 700;
	text-transform: uppercase;
	letter-spacing: 0.2em;
	color: #f9f9f9;
	padding: 15px 24px;
	border: 2px solid #ffffff;
	border-radius: 28px;

	&_green {
		font-size: 18px;
		line-height: 32px;
		font-weight: 700;
		text-transform: uppercase;
		color: #2ebd59;
		padding: 12px 16px;
		border: 2px solid #2ebd59;
		border-radius: 24.5px;
	}

	@media (max-width: 1275px) {
		font-size: 14px;
		padding: 10px 16px;
		border-radius: 20px;

		&_green {
			font-size: 14px;
			padding: 8px 12px;
			border-radius: 18.5px;
		}
	}
}



/* ==========================================================================
FOOTER
========================================================================== */

.footer {
	background-color: #212227;
	width: 100%;
	margin: 0;

	&__row {
		@include flexbox();
		padding: 97px 0 68px;

		p {
			font-size: 14px;
			font-weight: 300;
			line-height: 21px;
			letter-spacing: 0;
			color: #919191;
		}
	}

	&__column {
		flex: 0 1 33.333%;
	}

	&__about {
		flex: 0 1 28.814%;
		padding: 0 5.1% 0 0;
	}

	&__contact {
		@include flexbox();
		flex-direction: column;
		flex: 0 1 25.425%;
		padding: 0 6% 0 5%;
	}

	&__subscribe {
		flex: 0 1 45.761%;
		padding: 0 0 0 5%;
	}

	&__title {
		margin: 0 0 24px 0;

		&_first {
			margin: 0 0 26px 0;
		}
	}

	&__text {
		margin: 0 0 20px;
		&:last-child {margin: 0;}
	}

	&__block {
		margin: 0 0 74px;
	}

	&__copyright {
		text-transform: uppercase;
	}

	&__form {
		display: flex;
		max-width: 480px;
		height: 60px;
		margin-top: 16px;
		input[type="text"] {
			width: 100%;
			height: 60px;
			background-color: #4d4e52;
			text-transform: uppercase;
			border: none;
			&::placeholder {
				color: #a4a4a4;
				padding: 20px 0 0 20px;
			}
		}
		button.success {
			border: none;
			text-align: center;
			width: 55px;
			height: 60px;
			background-color: #4d4e52;
			i {
				width: 55px;
				font-size: 18px;
				color: #a4a4a4;
				text-align: center;

			}
		}
	}
	@media (max-width: 1000px) {
		&__row {
			padding: 40px 0;
		}
	}
	@media (max-width: 800px) {
		&__row {
			flex-direction: column;
		}
		&__about {
			@include flexorder(3);
		}
		&__contact {
			@include flexorder(1);
			flex-direction: row;
			justify-content: space-between;
			padding: 0;
		}
		&__subscribe {
			@include flexorder(2);
		}
		&__about, &__contact, &__subscribe {
			margin: 0 0 10px;
			padding: 0 4%;
		}
		&__block {
			margin: 0 3% 20px 0;
		}
	}

	@media (max-width: 500px) {
		&__about, &__contact, &__subscribe {
			padding: 0;
		}
	}
}

.title-footer {
	font-size: 18px;
	line-height: 1.2;
	font-weight: 700;
	text-transform: uppercase;
	color: #f9f9f9;
	max-width: 150px;
}

.social {
	@include flexbox();
	max-width: 110px;
	margin: 0 0 10px;
	color: #919191;
	&__icon {
		@include flexbox();
		justify-content: center;
		align-items: center;
		min-width: 14px;
		margin: 0 16px 0 0;
		i {
			font-size: 14px;
		}
	}

	&__link {
		font-size: 14px;
		font-weight: 700;
		text-transform: uppercase;
	}

	&:hover {
		color: #ffffff;
		transition: 0.5s;
	}
}

.block-sponsor {
	@include flexbox();
	flex-direction: row;

	&__logo {
		@include flexbox();
		justify-content: center;
		align-items: center;
		width: 80px;
		height: 80px;
		border: 1px solid #f4f8fb;
		margin: 0 20px 0 0;
		&:last-child {
			margin: 0;
		}
	}
	p {
		font-family: "Lato";
		font-size: 12px;
		font-weight: 700;
		text-transform: uppercase;
		color: #f2f7fb;
	}
}




// ==========================================================================

.ibg {
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
	img {display: none;}
}
